.box-container{
  padding: 1em;
}

.box{
  background: #fff;
  max-width: 900px;
  padding: 1.5em 2em;
  text-align: justify;
  text-justify: inter-word;
  border: 1.4px solid lightgrey;
  border-radius: 8px;
  margin: 20px auto 50px;
}
.nav-logo{
    height: 48px;
    position: absolute;
    top: 3px;
    left: 8px;
}
.brand-name{
    font-size: 17px;
    position: absolute;
    top: 13px;
    left: 70px;
}
.home-top-img {
    width: 100%;
    max-width: 820px;
    margin-left: auto;
    margin-right: auto;
}


.notice-container{
    background: #fff;
    height: 500px;
    margin:10px; 
    border-radius: 8px;
    overflow: hidden;
}
.info-container{
    background: #fff;
    height: 500px;
    margin:10px;  
    overflow: hidden;
}

.notice-container .header,
.info-container .header{
    padding: 10px; 
    padding-bottom:0;
}

.notice-container .body,
.info-container .body {
    height: 390px;
    margin: 0 10px; 
    overflow-x:hidden;
    overflow-y: auto; 
    padding: 1em;
}

@media only screen and (max-width: 990px){
    .home-header{
        text-align: center;
    }
}


.AboutCollege-container{
    padding: 1em;
}

.about-college{
    background: #fff;
    max-width: 900px;
    padding: 1.5em 2em;
    text-align: justify;
    text-justify: inter-word;
    border: 1.4px solid lightgrey;
    border-radius: 8px;
    margin: 20px auto 50px;
}
.gallery-container .carousel{
    display: block;
    width:100%;
    max-width: 1200px;
    height: 500px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}
.galery-carousel{
    display: block;
    width:100%;
    max-width: 1200px;
    height: 500px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    padding: 2em 0;
    margin-bottom: 100px;
}

.gallery-image{
    max-width: 100%;
    width: 500px;
}
.box{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.teacher-box-container{
  margin: 1em;
}

.teacher-box{
  background: #f4f7fc;
  padding: 1rem;
  border-radius: 8px;
  width: 300px;
  text-align: center;
}

.teacher-box-header img{
  border-radius: 100%;
  margin: 1em 0;
}

.teacher-name{
  font-weight: bold;
  color: #202e47;
  font-size: 1.3em;
}
.teacher-data {
  color: rgb(7, 55, 93);
}
.footer-container{
    background-color: #3c4049;
    color: white;
    position: relative;
    width: 100%;
}

.footer-top{
    font-size: 0.8em;
    padding-top: 15px;
    text-align: center;
}

.footer-bottom{
    background-color: #30343d;
    width: 100%;
    bottom: 0;
    padding: 5px 0;
    font-size: 0.8em;
    text-align: center;
}

@media only screen and (min-width: 990px){
    .footer-container .social-link-container ul li{
        top: 15px;
    }
}

.social-link-container{
    text-align: center;
}
.social-link-container ul{
    display: -webkit-flex;
    display: flex;
    list-style: none;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.social-link-container ul li{
    font-size: 20px;
    padding: 0.1em 0.5em;
    width: 35px;
    height: 35px;
    background-color: #30343d;
    border-radius: 100%;
    margin: 0;
    position: relative;
    right: 20px;
}

.social-link-container ul li a{
    color: #fff;
}
.social-link-container ul li a:hover{
    color: skyblue;
}

